span {
    display: block;
    text-align: left;
    width: 80%;
    margin: 0 auto 6px auto;
}
.card {
    width: 23%;
    margin: 0 1%;
    /* height: 86.1%; */
    height: 501px;
    background-color: white;
    border-radius: 20px;
    text-align: center;
    padding-top: 20px;
    transition: ease-in-out 0.5s;
    font-size: 0;
    border: 1px solid whitesmoke;
    box-shadow: 0 19px 38px rgba(99, 98, 98, 0.1),
    0 15px 12px rgba(99, 98, 98, 0.1);
}
.card:hover {
    cursor: pointer;
    transform: scale(1.05);
}
.card a {
    width: 100%;
    height: 100%;
    color: black;
}
.title {
    height: 40px;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 15px;
}
.content {
    height: 30px;
    font-size: 14px;
}
embed {
    width: 80%;
    height: 340px;
    margin: 4% 0 10% 0;
    border-radius: 20px;
}
.home-wrap{
    width: 100%;
}
.home-wrap .wrap {
    width: 100%;
    display: flex;
}
section {
    margin-bottom: 100px;
    padding: 0 5%;
}
h3 {
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: bold;
    letter-spacing: 1px;
}
.subject {
    font-size: 10px;
}
.home-wrap #btn>button{
    background-color: transparent;
    border-style: none;
    cursor: pointer;
}
.home-wrap #btn p{
    font-weight: bold;
    color: #787878;
    text-shadow: 2px 2px 2px 2px; 
}
.home-wrap #btn{
    position: sticky;
    float: right;
    bottom: 20px;
}